.about {
  background-color: var(--background-light);
  color: var(--text-primary-light);
}

.about-hero {
  background: linear-gradient(135deg, var(--primary-dark-blue), var(--secondary-dark-blue));
  color: var(--text-primary-dark); /* Keeping white text for contrast on blue background */
  padding: 8rem 2rem;
  text-align: center;
  padding-bottom: 10rem;
  position: relative;
}

.about-hero h1 {
  font-size: clamp(2.5rem, 5vw, 3.5rem);
  margin-bottom: 1.5rem;
}

.lead {
  font-size: clamp(1rem, 2vw, 1.25rem);
  max-width: 600px;
  margin: 0 auto;
  line-height: 1.6;
}

.our-story {
  padding: 6rem 0;
  background-color: var(--background-light);
  position: relative;
  padding-top: 10rem;
  padding-bottom: 10rem;
}

.story-grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 4rem;
  align-items: start;
}

.story-content {
  padding-right: 2rem;
}

.story-content h2 {
  color: var(--primary-dark-blue);
  margin-bottom: 2rem;
}

.story-content p {
  margin-bottom: 1.5rem;
  line-height: 1.6;
  color: var(--text-primary-light);
}

.story-timeline {
  display: grid;
  gap: 2rem;
}

.timeline-item {
  background-color: var(--card-background-light);
  padding: 2rem;
  border-radius: var(--border-radius-standard);
  position: relative;
  box-shadow: var(--shadow-standard-light);
}

.year {
  color: var(--primary-dark-blue);
  font-weight: 700;
  font-size: 1.25rem;
  margin-bottom: 0.5rem;
}

.values {
  background-color: var(--off-white);
  padding: 6rem 0;
  position: relative;
  padding-top: 10rem;
  padding-bottom: 10rem;
}

.values-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 2rem;
  margin-top: 3rem;
}

.value-card {
  background-color: var(--card-background-light);
  padding: 2rem;
  border-radius: var(--border-radius-standard);
  text-align: center;
  transition: var(--transition-standard);
  box-shadow: var(--shadow-standard-light);
}

.value-card:hover {
  transform: translateY(-5px);
}

.value-icon {
  font-size: 2.5rem;
  margin-bottom: 1rem;
}

.team {
  padding: 6rem 0;
  background-color: var(--background-light);
  position: relative;
  padding-top: 10rem;
  padding-bottom: 10rem;
}

.team-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 3rem;
  margin-top: 3rem;
}

.team-member {
  text-align: center;
  background-color: var(--card-background-light);
  padding: 2rem;
  border-radius: var(--border-radius-standard);
  box-shadow: var(--shadow-standard-light);
}

.member-photo {
  width: 150px;
  height: 150px;
  border-radius: 50%;
  background-color: var(--light-gray);
  background-size: cover;
  background-position: center;
  margin: 0 auto 1.5rem;
}


.role {
  color: var(--primary-dark-blue);
  font-weight: 600;
  margin: 0.5rem 0;
}

.partners {
  background-color: var(--off-white);
  padding: 6rem 0;
  position: relative;
  padding-top: 10rem;
  padding-bottom: 10rem;
}

.partners-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 3rem;
  margin-top: 3rem;
  justify-items: center;
}

.partner-logo {
  background-color: var(--card-background-light);
  padding: 2rem;
  border-radius: var(--border-radius-standard);
  width: 100%;
  text-align: center;
  font-weight: 600;
  color: var(--text-primary-light);
  box-shadow: var(--shadow-standard-light);
}

.join-cta {
  background: linear-gradient(135deg, var(--primary-dark-blue), var(--secondary-dark-blue));
  color: var(--text-primary-dark); /* Keeping white text for contrast on blue background */
  padding: 6rem 0;
  text-align: center;
  position: relative;
  padding-top: 10rem;
  padding-bottom: 10rem;
}

.join-cta h2 {
  margin-bottom: 1rem;
  color: var(--text-primary-dark)
}

.join-cta p {
  margin-bottom: 2rem;
}


/* Section headers */
section h2 {
  color: var(--text-primary-light);
  margin-bottom: 2rem;
}

@media (max-width: 768px) {
  .about-hero h1 {
    font-size: 2.5rem;
  }

  .story-grid {
    grid-template-columns: 1fr;
  }

  .story-content {
    padding-right: 0;
  }
  
  section {
    padding: 4rem 0;
    padding-top: 8rem;
  }
}
